.App {
  text-align: center;
}

.lavender-nav {
  display: flex;
  background-color: lavender;
  width: 100%;
  color: #fff;
  padding-left: 20px;
}

div {
  box-sizing: border-box;
}

.list {
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid darkgray;
}

.modal {
  position: absolute;
  width: 80%;
  height: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  padding: 30px;
  background: #eee;
  text-align: left;
}